</template>

<style lang="scss">
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
#nav {
  padding: 30px;
  a {
    font-weight: bold;
    color: #2c3e50;
    &.router-link-exact-active {
      color: #42b983;
    }
  }
}
</style>
<script>
    import Reg from "./pages/Reg";
    import Login from "./pages/Login";
    import Header from './pages/Header';
    import Footer from "./components/Footer";
    import Home from "./pages/Home";
    import GroupShop from "./pages/GroupShop";
    import Shang from "./components/Shang";
    import Vip from "./pages/Vip";
    export default {

        components: {Vip, Shang, GroupShop, Home, Footer, Login, Reg,Header},
        watch:{
            $route:{//路由监听-》数据观测
                handler(to){
                    let path = to.path;
                    console.log(path)

                    if(/home/.test(path)){
                        this.$root.$data.bNav=true;
                        this.$root.$data.bFoot=true;
                    }
                    if(/detail|login|reg/.test(path)){
                        this.$root.$data.bNav=false;
                        this.$root.$data.bFoot=false;
                    }
                    if(/vip|groupshop/.test(path)){
                        this.$root.$data.bNav=false;
                        this.$root.$data.bFoot=true;
                    }

                },
                immediate:true
            }
        },
    }
</script>