<template>
    <div>
        <Silder/>
        <div class="am-cf am-g my-shop-nav">
            <div class="my-shop-nav-panellf">
                <div class=" my-shop-nav-img am-circle ">
                    <img src="../assets/default/img1.jpg" class="am-circle m-img-thumbnail" />
                </div>
            </div>
            <div class="my-shop-nav-panelrt am-fr">
                <ul class="am-nav am-nav-pills my-shop-nav-ul">
                    <router-link tag="li" to="/home"><a href="javscript:;">关于我们</a></router-link>
                    <router-link tag="li" to="/vip"><a href="javscript:;">新闻中心</a></router-link>
                    <router-link tag="li" to="/groupshop"><a href="javscript:;">团购</a></router-link>
                </ul>
            </div>
        </div>
        <List :list="list" dataname="home"/>
    </div>

</template>

<script>
    import axios from 'axios'
    import Silder from "../components/Silder";
    import Nav from "../components/Nav";
    import List from "../components/List";
    export default {
        name: "Home",
        components:{List, Nav, Silder},
        data(){
          return{
              list:[]
          }
        },
        /*beforeRouteEnter(to,from,next){
            axios({url:'/api/user'}).then(
                res=>{
                    if (res.data.error===1){
                        console.log('路由独享守卫未登录',res.data)
                        next('/login')
                    } else if (res.data.error===0){
                        next()
                    }else {
                        console.log('路由独享守卫未登录',res.data)
                        next('/login')
                    }
                }
            )
        },*/
        created(){
            /*
            * 相对路径 指向assets开发目录
				绝对路径 指向public目录

				在 JavaScript 被导入或在 template/CSS 中通过相对路径被引用。这类引用会被 webpack 处理。
				放置在 public 目录下或通过绝对路径被引用。这类资源将会直接被拷贝，而不会经过 webpack 的处理。

				src内部的资源会优化，public不会优化

				url-loader 将小于 10kb 的资源内联，以减少 HTTP 请求的数量
					10kb以下的资源，压缩转换
					image会变成base64

				推荐 资源放置到assets:

					脚本和样式表会被压缩且打包在一起，从而避免额外的网络请求。
					文件丢失会直接在编译时报错，而不是到了用户端才产生 404 错误。
					最终生成的文件名包含了内容哈希，因此你不必担心浏览器会缓存它们的老版本

				何时使用 public 文件夹

					你需要在构建输出中指定一个文件的名字。
					你有上千个图片，需要动态引用它们的路径。
					有些库可能和 webpack 不兼容，这时你除了将其用一个独立的 <script> 标签引入没有别的选择。*/
            // console.log(axios)
            //绝对路径/是到public，@到src
            axios({url:'/json/home.json'}).then(res=>
                {
                    // console.log(res.data)
                    this.list=res.data
                }
            )
        }
    }
</script>

