<template>
    <div class="container">
        <header data-am-widget="header" class="am-header am-header-default my-header">
            <div class="am-header-left am-header-nav">
                <a href="javascript:;" class="" @click="goback">
                    <i class="am-header-icon am-icon-chevron-left"></i>
                </a>
            </div>
            <h1 class="am-header-title">
                <a href="#title-link" class="">用户登录</a>
            </h1>
            <div class="am-header-right am-header-nav">
                <a href="javascript:;" class="" @click="gohome">
                    <i class="am-header-icon  am-icon-home"></i>
                </a>
            </div>
        </header>
        <div class="cart-panel">
            <form class="am-form">
                <div class="am-form-group am-form-icon">
                    <i class="am-icon-user" style="color:#329cd9"></i>
                    <input type="text" class="am-form-field  my-radius" placeholder="请输入您的用户名" v-model="username">
                </div>
                <div class="am-form-group am-form-icon">
                    <i class="am-icon-lock" style="color:#329cd9"></i>
                    <input v-model="password" type="password" class="am-form-field  my-radius" placeholder="请输入您的密码" >
                </div>
                <div class="am-checkbox">
                    <label>
                        <input type="checkbox"> 记住密码
                    </label>
                </div>
                <div class="am-checkbox">
                    {{mess}}
                </div>
                <p class="am-text-center"><button type="button" class="am-btn am-btn-danger am-radius am-btn-block" @click="login">立即登录</button></p>
            </form>
        </div>

        <footer data-am-widget="footer" class="am-footer am-footer-default" data-am-footer="{  }">
            <hr data-am-widget="divider" style="" class="am-divider am-divider-default"/>
            <div class="am-footer-miscs ">
                <p>CopyRight©2014 AllMobilize Inc.</p>
                <p>京ICP备13033158</p>
            </div>
        </footer>
        <!--底部-->
        <div data-am-widget="navbar" class="am-navbar am-cf my-nav-footer " id="">
            <ul class="am-navbar-nav am-cf am-avg-sm-4 my-footer-ul">
                <li>
                    <a href="/wap/" class="">
                        <span class="am-icon-home"></span>
                        <span class="am-navbar-label">首页</span>
                    </a>
                </li>
                <li>
                    <a href="###" class="">
                        <span class=" am-icon-phone"></span>
                        <span class="am-navbar-label">电话</span>
                    </a>
                </li>
                <li>
                    <a href="###" class="">
                        <span class=" am-icon-comments"></span>
                        <span class="am-navbar-label">聊天</span>
                    </a>
                </li>
                <li>
                    <a href="#" class="">
                        <span class=" am-icon-map-marker"></span>
                        <span class="am-navbar-label">地图</span>
                    </a>
                </li>
                <li style="position:relative">
                    <a href="javascript:;" onClick="showFooterNav();" class="">
                        <span class="am-icon-user"></span>
                        <span class="am-navbar-label">会员</span>
                    </a>
                    <div class="footer-nav" id="footNav">
                        <span class=" am-icon-bank"><a href="#">买家中心</a></span>
                        <span class="am-icon-suitcase"><a href="#">卖家中心</a></span>
                        <span class="am-icon-usd"><a href="#">我的钱包</a></span>
                        <span class="am-icon-user"><a href="#">个人资料</a></span>
                        <span class="am-icon-th-list"><a href="#">帮助中心</a></span>
                        <span class="am-icon-comments"><a href="#">消息中心</a></span>
                        <span class="am-icon-power-off"><a href="#">安全退出</a></span>
                    </div>
                </li>
            </ul>

        </div>
    </div>
</template>

<script>
    export default {
        name: "Login",
        data(){
            return {
                username:'',
                password:'',
                mess:'',

            }
        },
        methods:{
            goback(){
                this.$router.go(-1)
            },
            gohome(){
              this.$router.push('/home')
            },
            showFooterNav(){
                $("#footNav").toggle();
            },
            login(){
                axios({
                    url:'/api/login',
                    method:'post',
                    data:{username:this.username,password:this.password,save:true}
                }).then(
                    res=>{
                        if(res.data.err===0){
                            console.log(res.data);
                            this.$router.push('/home')
                        }else{
                            console.log(res.data);
                            this.mess = res.data.mess;
                            this.username='';
                            this.password='';
                        }
                    }
                )
            }
        },
    }
</script>

<style scoped>

</style>